import React from "react";
import useIterator from "./useIterator.js";

export default function RepoMenu({ repositories }) {
  let repoArray = Object.values(repositories);
  repoArray.splice(-1, 1);
  const [name, prev, next] = useIterator(repoArray);
  console.log(repositories);

  if (!name) return;

  return (
    <div style={{ display: "flex" }}>
      <button className="repoButton" onClick={prev}>
        &lt;
      </button>
      <a
        className="repoName"
        href={name.clone_url}
        target="_blank"
        rel="noreferrer"
      >
        {name.name}
      </a>
      <button className="repoButton" onClick={next}>
        &gt;
      </button>
    </div>
  );
}
